import { useEffect, useState } from 'react'

interface Countdown {
  years: number
  months: number
  days: number
  hours: number
  minutes: number
  seconds: number
}

type TimeUnit = 'years' | 'months' | 'days' | 'hours' | 'minutes' | 'seconds'

const useCountdown = (deadline: number, onComplete?: () => void, maxUnit: TimeUnit = 'seconds'): Countdown => {
  const calculateTimeLeft = (): Countdown => {
    const now = new Date()
    const difference = deadline - now.getTime()

    if (difference <= 0) {
      return {
        years: 0,
        months: 0,
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0
      }
    }

    const seconds = Math.floor((difference / 1000) % 60)
    const minutes = Math.floor((difference / 1000 / 60) % 60)
    const hours = Math.floor((difference / (1000 * 60 * 60)) % 24)
    const days = Math.floor((difference / (1000 * 60 * 60 * 24)) % 30)
    const months = Math.floor((difference / (1000 * 60 * 60 * 24 * 30)) % 12)
    const years = Math.floor(difference / (1000 * 60 * 60 * 24 * 365))

    // Â¶ÇÊûúÊúÄÂ§ßÂçï‰ΩçÊòØ daysÔºåÂàôÂ∞Ü months ËΩ¨Êç¢‰∏∫Â§©Êï∞Âπ∂Á¥ØÂä†
    if (maxUnit === 'days') {
      const daysInMonth = 30 // ÂÅáËÆæÊØè‰∏™Êúà 30 Â§©
      return {
        years: 0, // ‰∏çÊòæÁ§∫ years
        months: 0, // ‰∏çÊòæÁ§∫ months
        days: days + months * daysInMonth + years * 365, // Á¥ØÂä† years Âíå months ÁöÑÂ§©Êï∞
        hours,
        minutes,
        seconds
      }
    }

    // Â¶ÇÊûúÊúÄÂ§ßÂçï‰ΩçÊòØ monthsÔºåÂàôÂ∞Ü years ËΩ¨Êç¢‰∏∫Êúà‰ªΩÂπ∂Á¥ØÂä†
    if (maxUnit === 'months') {
      return {
        years: 0, // ‰∏çÊòæÁ§∫ years
        months: months + years * 12, // Á¥ØÂä† years ÁöÑÊúà‰ªΩ
        days,
        hours,
        minutes,
        seconds
      }
    }

    return { years, months, days, hours, minutes, seconds }
  }

  const [timeLeft, setTimeLeft] = useState<Countdown>(calculateTimeLeft())

  useEffect(() => {
    const now = new Date()
    const difference = deadline - now.getTime()
    if (difference <= 0) {
      return
    }

    const timer = setInterval(() => {
      const newTimeLeft = calculateTimeLeft()
      setTimeLeft(newTimeLeft)

      if (
        newTimeLeft.years === 0 &&
        newTimeLeft.months === 0 &&
        newTimeLeft.days === 0 &&
        newTimeLeft.hours === 0 &&
        newTimeLeft.minutes === 0 &&
        newTimeLeft.seconds === 0
      ) {
        clearInterval(timer)
        if (onComplete) {
          onComplete() // Ë∞ÉÁî®ÂõûË∞ÉÂáΩÊï∞
        }
      }
    }, 1000)

    return () => clearInterval(timer)
  }, [deadline])

  return timeLeft
}

export default useCountdown
